<template>
  <div v-loading="loading">
    <div class="handle-box">
      <div class="btn">
        <el-button type="primary" @click="addHouses" v-if="isShow"></el-button>
      </div>

      <div class="btn">
        <el-input
          v-model="house_id"
          placeholder=""
          class="input-with-select"
          @keyup.enter.native="search(house_id)"
        >
          <el-button slot="append" icon="el-icon-search" @click="search(house_id)"></el-button>
        </el-input>
      </div>
    </div>

    <el-dialog title="" :visible.sync="dialogHouses" width="80%">
      <div class="box">
        <el-form :model="form" label-width="130px">
          <el-form-item label="">
            <el-input v-model="form.name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-radio-group v-model="form.sex">
              <el-radio :label="1"></el-radio>
              <el-radio :label="2"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="">
            <v-map></v-map>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>

    <!--  -->
    <el-table :data="tableData" border :header-cell-style="{background:'#f0f0f0'}">
      <el-table-column prop="id" label="ID"></el-table-column>
      <el-table-column prop="address" label=""></el-table-column>
      <el-table-column prop="room_count" label=""></el-table-column>
      <el-table-column prop="room_resident" label=""></el-table-column>
      <el-table-column label="" width="400px">
        <template slot-scope="scope">
          <el-button type="primary" size="mini" @click="handleBuild(scope.$index, scope.row)"></el-button>
          <el-button
            type="primary"
            size="mini"
            @click="handleResident(scope.$index, scope.row)"
          ></el-button>
          <!-- <el-button type="primary" size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button> -->
          <!-- <el-button type="danger" size="mini" @click="handleDel(scope.$index, scope.row)"></el-button> -->
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="dialogResident"
      width="80%"
      @close="closeShowUser"
      :close-on-click-modal="false"
    >
      <div class="box">
        <div class="handle-box">
          <div class="btn">
            <el-select v-model="user" @change="handleUser" placeholder="">
              <el-option
                v-for="item in userList"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </div>
        </div>
        <template v-if="user == ''">
          <el-table :data="residentData" border :header-cell-style="{background:'#f0f0f0'}">
            <el-table-column prop="user_id" label="ID"></el-table-column>
            <el-table-column prop="room_id" label=""></el-table-column>
            <el-table-column prop="name" label=""></el-table-column>
            <el-table-column prop="type" label="">
              <template slot-scope="scope">
                <span v-if="scope.row.type == 1"></span>
                <span v-else-if="scope.row.type == 2"></span>
                <span v-else-if="scope.row.type == 4"></span>
              </template>
            </el-table-column>
            <el-table-column prop="card_number" label=""></el-table-column>
            <el-table-column prop="phone" label=""></el-table-column>
            <el-table-column prop="href" label="">
              <template slot-scope="scope">
                <div v-if="scope.row.href">
                  <el-popover placement="top-start" title trigger="click">
                    <img :src="scope.row.href" style="max-width:800px;max-height:800px;" />
                    <img
                      slot="reference"
                      :src="scope.row.href"
                      style="max-width:180px;max-height:80px;"
                    />
                  </el-popover>
                </div>
                <div v-else>
                  <span>----</span>
                </div>
              </template>
            </el-table-column>
            <el-table-column label="" align="center">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  size="mini"
                  @click="handleLogs(scope.$index, scope.row)"
                ></el-button>
                <el-button type="danger" size="mini" @click="handleDel(scope.$index, scope.row)"></el-button>
              </template>
            </el-table-column>
          </el-table>
          <div class="block">
            <el-pagination
              @current-change="handleCurrentResident"
              :current-page.sync="currentResidentPage"
              :page-sizes="[10, 20, 30, 40, 50]"
              :page-size="pageSizeResident"
              layout="sizes, prev, pager, next, jumper"
              :total="totalResidentPage"
              @size-change="handleSizeResident"
            ></el-pagination>
          </div>
        </template>
        <template v-if="user == ''">
          <el-table :data="houseOwnerList" border :header-cell-style="{background:'#f0f0f0'}">
            <el-table-column prop="user_id" label="ID"></el-table-column>
            <el-table-column prop="room_id" label=""></el-table-column>
            <el-table-column prop="name" label=""></el-table-column>
            <el-table-column prop="type" label="">
              <template slot-scope="scope">
                <span v-if="scope.row.type == 1"></span>
                <span v-else-if="scope.row.type == 2"></span>
                <span v-else-if="scope.row.type == 4"></span>
              </template>
            </el-table-column>
            <el-table-column prop="card_number" label=""></el-table-column>
            <el-table-column prop="phone" label=""></el-table-column>
            <el-table-column prop="href" label="">
              <template slot-scope="scope">
                <div v-if="scope.row.href">
                  <el-popover placement="top-start" title trigger="click">
                    <img :src="scope.row.href" style="max-width:800px;max-height:800px;" />
                    <img
                      slot="reference"
                      :src="scope.row.href"
                      style="max-width:180px;max-height:80px;"
                    />
                  </el-popover>
                </div>
                <div v-else>
                  <span>----</span>
                </div>
              </template>
            </el-table-column>
            <el-table-column label="" align="center">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  size="mini"
                  @click="handleLogs(scope.$index, scope.row)"
                ></el-button>
                <el-button type="danger" size="mini" @click="handleDel(scope.$index, scope.row)"></el-button>
              </template>
            </el-table-column>
          </el-table>
          <div class="block">
            <el-pagination
              @current-change="handleCurrentHouseOwner"
              :current-page.sync="houseOwnerCurrentPage"
              :page-sizes="[10, 20, 30, 40, 50]"
              :page-size="houseOwnerPageSize"
              layout="sizes, prev, pager, next, jumper"
              :total="houseOwnerTotalPage"
              @size-change="handleSizeHouseOwner"
            ></el-pagination>
          </div>
        </template>
        <template v-if="user == ''">
          <el-table :data="renterList" border :header-cell-style="{background:'#f0f0f0'}">
            <el-table-column prop="user_id" label="ID"></el-table-column>
            <el-table-column prop="room_id" label=""></el-table-column>
            <el-table-column prop="name" label=""></el-table-column>
            <el-table-column prop="type" label="">
              <template slot-scope="scope">
                <span v-if="scope.row.type == 1"></span>
                <span v-else-if="scope.row.type == 2"></span>
                <span v-else-if="scope.row.type == 4"></span>
              </template>
            </el-table-column>
            <el-table-column prop="card_number" label=""></el-table-column>
            <el-table-column prop="phone" label=""></el-table-column>
            <el-table-column prop="href" label="">
              <template slot-scope="scope">
                <div v-if="scope.row.href">
                  <el-popover placement="top-start" title trigger="click">
                    <img :src="scope.row.href" style="max-width:800px;max-height:800px;" />
                    <img
                      slot="reference"
                      :src="scope.row.href"
                      style="max-width:180px;max-height:80px;"
                    />
                  </el-popover>
                </div>
                <div v-else>
                  <span>----</span>
                </div>
              </template>
            </el-table-column>
            <el-table-column label="" align="center">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  size="mini"
                  @click="handleLogs(scope.$index, scope.row)"
                ></el-button>
                <el-button type="danger" size="mini" @click="handleDel(scope.$index, scope.row)"></el-button>
              </template>
            </el-table-column>
          </el-table>
          <div class="block">
            <el-pagination
              @current-change="handleCurrentRenter"
              :current-page.sync="renterCurrentPage"
              :page-sizes="[10, 20, 30, 40, 50]"
              :page-size="renterPageSize"
              layout="sizes, prev, pager, next, jumper"
              :total="renterTotalPage"
              @size-change="handleRenterSize"
            ></el-pagination>
          </div>
        </template>
        <template v-if="user == ''">
          <el-table :data="managementList" border :header-cell-style="{background:'#f0f0f0'}">
            <el-table-column prop="user_id" label="ID"></el-table-column>
            <el-table-column prop="room_id" label=""></el-table-column>
            <el-table-column prop="name" label=""></el-table-column>
            <el-table-column prop="type" label="">
              <template slot-scope="scope">
                <span v-if="scope.row.type == 1"></span>
                <span v-else-if="scope.row.type == 2"></span>
                <span v-else-if="scope.row.type == 4"></span>
              </template>
            </el-table-column>
            <el-table-column prop="card_number" label=""></el-table-column>
            <el-table-column prop="phone" label=""></el-table-column>
            <el-table-column prop="href" label="">
              <template slot-scope="scope">
                <div v-if="scope.row.href">
                  <el-popover placement="top-start" title trigger="click">
                    <img :src="scope.row.href" style="max-width:800px;max-height:800px;" />
                    <img
                      slot="reference"
                      :src="scope.row.href"
                      style="max-width:180px;max-height:80px;"
                    />
                  </el-popover>
                </div>
                <div v-else>
                  <span>----</span>
                </div>
              </template>
            </el-table-column>
            <el-table-column label="" align="center">
              <template slot-scope="scope">
                <el-button
                  type="primary"
                  size="mini"
                  @click="handleLogs(scope.$index, scope.row)"
                ></el-button>
                <el-button type="danger" size="mini" @click="handleDel(scope.$index, scope.row)"></el-button>
              </template>
            </el-table-column>
          </el-table>
          <div class="block">
            <el-pagination
              @current-change="handleManagement"
              :current-page.sync="managementCurrentPage"
              :page-sizes="[10, 20, 30, 40, 50]"
              :page-size="managementPageSize"
              layout="sizes, prev, pager, next, jumper"
              :total="managementTotalPage"
              @size-change="handlemanagementSize"
            ></el-pagination>
          </div>
        </template>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogBuild">
      <div class="box">
        <div class="handle-box">
          <div class="btn">
            <el-button type="primary" @click="HandleAddBuild"></el-button>
          </div>
        </div>
        <el-table :data="buildingList" border :header-cell-style="{background:'#f0f0f0'}">
          <el-table-column prop="door_number" label=""></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-button
                type="danger"
                size="mini"
                @click="HandledelBuilding(scope.$index, scope.row)"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!--  -->
      <div class="block">
        <el-pagination
          @current-change="handleCurrenBuildingt"
          :current-page.sync="currentBuildingPage"
          :page-sizes="[10, 20, 30, 40, 50]"
          :page-size="pageSizeBuilding"
          layout="sizes, prev, pager, next, jumper"
          :total="totalBuildingPage"
          @size-change="handleSizeBuilding"
        ></el-pagination>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog
      :visible.sync="dialogDelBuild"
      title=""
      width="20%"
      align="center"
      :close-on-click-modal="false"
    >
      <div style="font-size: 20px; margin-bottom: 30px;"></div>
      <span>
        <el-button type="primary" @click="toDelBuild"></el-button>
        <el-button type="danger" @click="dialogDelBuild = false"></el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogAddBuild">
      <div class="box">
        <el-form :model="buildForm" label-width="100px">
          <div v-for="(item,index) in buildForm.rooms" :key="index">
            <el-form-item label="">
              <el-input v-model="buildForm.rooms[index]" placeholder=""></el-input>
            </el-form-item>
          </div>
          <el-form-item label="">
            <el-button type="primary" @click="addRooms"></el-button>
            <el-button type="primary" @click="delRooms"></el-button>
          </el-form-item>
          <div class="submit">
            <el-form-item>
              <el-button type="primary" @click="newRooms"></el-button>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogLogs">
      <div class="box">
        <el-table :data="logsData" border :header-cell-style="{background:'#f0f0f0'}">
          <el-table-column prop="id" label="ID"></el-table-column>
          <el-table-column prop="number" label=""></el-table-column>
          <el-table-column prop="time" label=""></el-table-column>
          <el-table-column prop="direction" label="">
            <template slot-scope="scope">
              <span v-if="scope.row.direction == 1"></span>
              <span v-else-if="scope.row.direction == 2"></span>
            </template>
          </el-table-column>
          <el-table-column prop="image" label="">
            <template slot-scope="scope">
              <div v-if="scope.row.image">
                <el-popover placement="top-start" title trigger="click">
                  <img :src="scope.row.image" style="max-width:800px;max-height:800px;" />
                  <img
                    slot="reference"
                    :src="scope.row.image"
                    style="max-width:180px;max-height:80px;"
                  />
                </el-popover>
              </div>
              <div v-else>
                <span>----</span>
              </div>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="block">
        <el-pagination
          @current-change="handleCurrentLogs"
          :current-page.sync="currentLogsPage"
          :page-sizes="[10, 20, 30, 40, 50]"
          :page-size="pageSizeLogs"
          layout="sizes, prev, pager, next, jumper"
          :total="totalLogsPage"
          @size-change="handleSizeLogs"
        ></el-pagination>
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      :visible.sync="dialogDel"
      title=""
      width="20%"
      align="center"
      :close-on-click-modal="false"
    >
      <div style="font-size: 20px; margin-bottom: 30px;"></div>
      <span>
        <el-button type="primary" @click="toDel"></el-button>
        <el-button type="danger" @click="dialogDel = false"></el-button>
      </span>
    </el-dialog>

    <!--  -->
    <div class="block">
      <el-pagination
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-sizes="[10, 20, 30, 40, 50]"
        :page-size="pageSize"
        layout="sizes, prev, pager, next, jumper"
        :total="totalPage"
        @size-change="handleSizeChange"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import API from "@/api/index.js";
import vMap from "./map.vue";
import { log } from "util";

export default {
  components: {
    vMap,
  },
  data() {
    return {
      loading: false,
      dialogHouses: false,
      form: {
        name: "",
        sex: "",
        postion: "",
      },

      tableData: [], // 

      currentPage: 1, // 
      pageSize: 10,
      totalPage: 0,

      dialogResident: false, // 
      residentData: [],
      currentResidentPage: 1,
      pageSizeResident: 10,
      totalResidentPage: 0,
      dialogBuild: false, // 
      // rooms: '',
      // addrooms: [],
      buildForm: {
        address_id: "",
        rooms: [],
      },
      id: "",
      house_id: "", // 
      face_id: "",

      user: "", // 
      userList: [
        {
          value: 0,
          label: "",
        },
        {
          value: 1,
          label: "",
        },
        {
          value: 2,
          label: "",
        },
        {
          value: 3,
          label: "",
        },
      ],
      houseOwnerList: [], // 
      renterList: [], // 
      managementList: [], // 

      houseOwnerCurrentPage: 1, // 
      houseOwnerPageSize: 10,
      houseOwnerTotalPage: 0,

      renterCurrentPage: 1, // 
      renterPageSize: 10,
      renterTotalPage: 0,

      managementCurrentPage: 1, //
      managementPageSize: 10,
      managementTotalPage: 0,

      dialogLogs: false, // 
      logsData: [],
      currentLogsPage: 1,
      pageSizeLogs: 10,
      totalLogsPage: 0,
      dialogDel: false,
      buildingList: [], // 
      address_id: "",
      dialogAddBuild: false,
      currentBuildingPage: 1, // 
      pageSizeBuilding: 10,
      totalBuildingPage: 0,
      dialogDelBuild: false,
      building_id: "",
      permission: localStorage.getItem("permissions"),
      isShow: false,
    };
  },
  mounted() {
    this.getnewHouses();
    var permissionList = this.permission.split(",");
    // console.log(permissionList.includes('housesAdd'));
    if (permissionList.includes("housesAdd")) {
      this.isShow = true;
    }
  },
  methods: {
    // 
    getnewHouses() {
      var self = this;
      API.addresses(self.currentPage, self.pageSize).then((res) => {
        // console.log("", res.data);
        self.tableData = res.data;
        self.totalPage = res.total;
      });
    },

    // 
    search() {
      var self = this;
      API.addresses(self.currentPage, self.pageSize, 0, self.house_id).then(
        (res) => {
          self.tableData = res.data;
          self.totalPage = res.total;
          // self.house_id = "";
          self.$message.success("");
        }
      );
    },

    addHouses() {
      var self = this;
      self.dialogHouses = true;
    },

    newHouses() {
      var self = this;
    },

    // 
    handleBuild(index, row) {
      var self = this;
      self.dialogBuild = true;
      self.buildForm.address_id = row.id;
      self.address_id = row.id;
      self.getBuilding();
    },
    HandleAddBuild(index, row) {
      var self = this;
      self.dialogAddBuild = true;
    },
    // 
    getBuilding() {
      var self = this;
      API.gainRooms(
        self.currentBuildingPage,
        self.pageSizeBuilding,
        self.address_id
      ).then((res) => {
        // console.log("", res);
        self.buildingList = res.data;
        self.totalBuildingPage = res.total;
      });
    },

    // 
    addRooms() {
      var self = this;
      self.buildForm.rooms.push("");
    },
    delRooms() {
      var self = this;
      self.buildForm.rooms.pop("");
    },

    newRooms() {
      var self = this;
      API.rooms(self.buildForm).then((res) => {
        self.$message.success("");
        self.dialogAddBuild = false;
        self.buildForm.rooms = [];
        self.rooms = "";
        self.getBuilding();
      });
    },

    // 
    handleResident(index, row) {
      var self = this;
      self.dialogResident = true;
      self.address_id = row.id;
      console.log(self.house_id);
      console.log(self.user);
      API.addressResidents(
        self.currentResidentPage,
        self.pageSizeResident,
        self.address_id
      ).then((res) => {
        console.log("", res);
        self.residentData = res.data;
        self.totalResidentPage = res.total;
      });
    },
    handleUser(value) {
      var self = this;
      switch (value) {
        case 0:
          self.$nextTick(() => {
            self.user = "";
            API.addressResidents(
              self.currentResidentPage,
              self.pageSizeResident,
              self.address_id
            ).then((res) => {
              self.residentData = res.data;
              self.totalResidentPage = res.total;
            });
          });
          break;
        case 1:
          self.$nextTick(() => {
            self.user = "";
            API.addressResidents(
              self.houseOwnerCurrentPage,
              self.houseOwnerPageSize,
              self.address_id,
              1
            ).then((res) => {
              console.log("", res);
              self.houseOwnerList = res.data;
              self.houseOwnerTotalPage = res.total;
            });
          });
          break;
        case 2:
          self.$nextTick(() => {
            self.user = "";
            API.addressResidents(
              self.renterCurrentPage,
              self.renterPageSize,
              self.address_id,
              2
            ).then((res) => {
              self.renterList = res.data;
              self.renterTotalPage = res.total;
            });
          });
          break;
        case 3:
          self.$nextTick(() => {
            self.user = "";
            API.addressResidents(
              self.managementCurrentPage,
              self.managementPageSize,
              self.address_id,
              4
            ).then((res) => {
              self.managementList = res.data;
              self.managementTotalPage = res.total;
            });
          });
          break;
      }
    },
    closeShowUser() {
      var self = this;
      self.user = "";
      self.residentData = [];
      self.houseOwnerList = [];
      self.renterList = [];
      self.managementList = [];
      self.currentResidentPage = 1;
    },

    // 
    handleLogs(index, row) {
      var self = this;
      self.dialogLogs = true;
      self.face_id = row.face_id;
      console.log(row);
      self.getFaceLogs();
    },
    getFaceLogs() {
      var self = this;
      API.faceLogs(self.currentLogsPage, self.pageSizeLogs, self.face_id).then(
        (res) => {
          console.log("getFaceLogs", res);
          self.logsData = res.data;
          self.totalLogsPage = res.total;
        }
      );
    },

    // 
    // handleEdit() {},
    // --
    handleDel(index, row) {
      var self = this;
      self.id = row.id;
      self.dialogDel = true;
    },
    toDel() {
      var self = this;
      API.delHousehold(self.id).then((res) => {
        switch (self.user) {
          case "":
            self.$message.success("");
            self.dialogDel = false;
            API.addressResidents(
              self.currentResidentPage,
              self.pageSizeResident,
              self.address_id
            ).then((res) => {
              console.log("", res);
              self.residentData = res.data;
              self.totalResidentPage = res.total;
            });
            break;
          case "":
            self.$message.success("");
            self.dialogDel = false;
            API.addressResidents(
              self.houseOwnerCurrentPage,
              self.houseOwnerPageSize,
              self.address_id,
              1
            ).then((res) => {
              console.log("", res);
              self.houseOwnerList = res.data;
              self.houseOwnerTotalPage = res.total;
            });
            break;
          case "":
            self.$message.success("");
            self.dialogDel = false;
            API.addressResidents(
              self.renterCurrentPage,
              self.renterPageSize,
              self.address_id,
              2
            ).then((res) => {
              self.renterList = res.data;
              self.renterTotalPage = res.total;
            });
            break;
          case "":
            self.$message.success("");
            self.dialogDel = false;
            API.addressResidents(
              self.managementCurrentPage,
              self.managementPageSize,
              self.address_id,
              4
            ).then((res) => {
              self.managementList = res.data;
              self.managementTotalPage = res.total;
            });
            break;
        }
      });
    },

    // --
    HandledelBuilding(index, row) {
      var self = this;
      self.dialogDelBuild = true;
      self.building_id = row.id;
    },
    toDelBuild() {
      var self = this;
      API.delBuilding(self.building_id).then((res) => {
        self.dialogDelBuild = false;
        self.$message.success("");
        self.getBuilding();
      });
    },

    // 
    handleCurrentChange(val) {
      var self = this;
      self.currentPage = val;
      if (self.house_id == "") {
        API.addresses(val, self.pageSize).then((res) => {
          self.tableData = res.data;
          self.totalPage = res.total;
        });
      } else {
        API.addresses(val, self.pageSize, 0, self.house_id).then((res) => {
          self.tableData = res.data;
          self.totalPage = res.total;
        });
      }
    },
    // 
    handleSizeChange(val) {
      var self = this;
      self.pageSize = val;
      if (self.house_id == "") {
        API.addresses(self.currentPage, val).then((res) => {
          self.tableData = res.data;
          self.totalPage = res.total;
        });
      } else {
        API.addresses(self.currentPage, val, 0, self.house_id).then((res) => {
          self.tableData = res.data;
          self.totalPage = res.total;
        });
      }
    },

    // 
    handleCurrentResident(val) {
      var self = this;
      self.currentResidentPage = val;
      API.addressResidents(val, self.pageSizeResident, self.address_id).then(
        (res) => {
          self.residentData = res.data;
        }
      );
    },
    handleSizeResident(val) {
      var self = this;
      self.pageSizeResident = val;
      API.addressResidents(self.currentResidentPage, val, self.address_id).then(
        (res) => {
          self.residentData = res.data;
        }
      );
    },

    // 
    handleCurrentHouseOwner(val) {
      var self = this;
      self.houseOwnerCurrentPage = val;
      API.addressResidents(
        val,
        self.houseOwnerPageSize,
        self.address_id,
        1
      ).then((res) => {
        self.houseOwnerList = res.data;
      });
    },
    //
    handleSizeHouseOwner(val) {
      var self = this;
      self.houseOwnerPageSize = val;
      API.addressResidents(
        self.houseOwnerCurrentPage,
        val,
        self.address_id,
        1
      ).then((res) => {
        self.houseOwnerList = res.data;
      });
    },

    // 
    handleCurrentRenter(val) {
      var self = this;
      self.renterCurrentPage = val;
      API.addressResidents(val, self.renterPageSize, self.address_id, 2).then(
        (res) => {
          self.renterList = res.data;
        }
      );
    },
    handleRenterSize(val) {
      var self = this;
      self.renterPageSize = val;
      API.addressResidents(
        self.renterCurrentPage,
        val,
        self.address_id,
        2
      ).then((res) => {
        self.renterList = res.data;
      });
    },

    // 
    handleManagement(val) {
      var self = this;
      self.managementCurrentPage = val;
      API.addressResidents(
        val,
        self.managementPageSize,
        self.address_id,
        4
      ).then((res) => {
        self.managementList = res.data;
      });
    },
    handlemanagementSize(val) {
      var self = this;
      self.managementPageSize = val;
      API.addressResidents(
        self.managementCurrentPage,
        val,
        self.address_id,
        4
      ).then((res) => {
        self.managementList = res.data;
      });
    },

    // 
    handleCurrentLogs(val) {
      var self = this;
      self.currentLogsPage = val;
      API.faceLogs(val, self.pageSizeLogs, self.face_id).then((res) => {
        self.logsData = res.data;
      });
    },
    handleSizeLogs(val) {
      var self = this;
      self.pageSizeLogs = val;
      API.faceLogs(self.currentLogsPage, val, self.face_id).then((res) => {
        self.logsData = res.data;
      });
    },

    // 
    handleCurrenBuildingt(val) {
      var self = this;
      self.currentBuildingPage = val;
      API.gainRooms(val, self.pageSizeBuilding, self.address_id).then((res) => {
        // console.log("", res);
        self.buildingList = res.data;
      });
    },
    handleSizeBuilding(val) {
      var self = this;
      self.pageSizeBuilding = val;
      API.gainRooms(self.currentBuildingPage, val, self.address_id).then(
        (res) => {
          // console.log("", res);
          self.buildingList = res.data;
        }
      );
    },
  },
};
</script>

